<template>
  <div id="index" v-cloak>
    <div class="index-top" style="margin-bottom: 63px;"></div>
    <div class="index-title flex-container flex-jus-s flex-warp" @click="$router.push('/mine')">
      <div class="index-head"><img :src="userInfos.headimgurl"/></div>
      <div class="index-name font15">{{userInfos.name}}</div>
      <div class="index-grade flex-container flex-jus-sa font14">
        <span class="color-99">本周排名：<i class="color-33">第{{userInfos.rank_no}}名</i></span>
        <span class="color-99"> 累计学习：<i class="color-33">{{userInfos.studytime}}天</i></span>
        <span class="color-99">累计完成：<i class="color-33">{{userInfos.curriculum}}节</i></span>
      </div>
    </div>
    <div class="index-study">
      <div class="flex-container flex-jus-sb padding-lef-right">
        <span class="font15 font-weight">{{recList.name}}</span>
        <span class="font14" @click="more(recList.id)">more</span>
      </div>
      <div class=" flex-jus-s flex-direction-row padd-t-10 padd-l-5 padd-r-7">
        <div class="card-wrap" style="padding: 0;">
          <div v-for="(i,k) in recList.childs" :key="k" @click="homeList(i.id)" class="card">
            <img :src="i.back_img ? i.back_img : defaultImg" alt="">
            <h2>{{i.name}}</h2>
            <p>{{i.num}}人正在学</p>
          </div>
        </div>
      </div>
    </div>
    <div class="index-study margin-t-15" v-if="popList.length">
      <div class="font15 flex-container flex-jus-s font-weight padding-lef-right">热门课程</div>
      <ul class="index-list">
        <li class="flex-container flex-jus-sb index-list-li flex-align-items" v-for="(i,index) in popList"
            @click="goDet(i.id)" :key="index">
          <div style="justify-content: flex-start; display: flex;padding-right: 0.4rem;">
            <div class="img"><img :src="hot_default_img" alt=""></div>
            <div class="flex-container flex-direction-col align-items-start" style="justify-content: center;">
              <span class="font15 padd-b-5 font-weight hot-li-span1" style="text-align:left">{{i.title}}</span>
              <span class="iconfont icon-ren font12 color-99"><em class="padd-l-20">人数: {{i.study_num}}</em></span>
            </div>
          </div>
          <div class="font12 flex-container flex-jus-c align-items-cen"
               :class="i.is_study==0 ? 'index-btn1':'index-btn'" v-text="i.is_study==0 ? '未学':'已学'"></div>
        </li>
      </ul>
    </div>
    <div class="index-study margin-t-25" v-if="reccoure.length">
      <div class="font15 flex-container flex-jus-s font-weight padding-lef-right">推荐课程</div>
      <ul class="index-list">
        <li class="flex-container flex-jus-sb index-list-li flex-align-items" v-for="(item,index) in reccoure"
            @click="goDet(item.id)" :key="index">
          <div style="justify-content: flex-start; display: flex;">
            <div class="img"><img :src="hot_default_img" alt=""></div>
            <div class="flex-container flex-direction-col align-items-start" style="justify-content: center;">
              <span class="font15 padd-b-5 font-weight hot-li-span1" style="text-align:left">{{item.title}}</span>
              <span class="iconfont icon-ren font12 color-99"><em class="padd-l-20">人数:{{item.study_num}}</em></span>
            </div>
          </div>
          <div class="font12 flex-container flex-jus-c align-items-cen"
               :class="item.is_study==0 ? 'index-btn1':'index-btn'"
               v-text="item.is_study==0 ? '未学':item.is_study==1 ? '已学':'已完成'"></div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import tianqi from "@/assets/images/icon/tianqi.png";
import wenlu from "@/assets/images/icon/wenlu.png";
import dadianhua from "@/assets/images/icon/dadianhua.png";
import lvyou from "@/assets/images/icon/lvyou.png";
import wenhua from "@/assets/images/icon/wenhua.png";
import meishi from "@/assets/images/icon/meishi.png";
import defaultImg from "@/assets/images/default.png";
import hot_default_img from "@/assets/images/hot_default_img.png";
import { Toast } from "vant";

Vue.use(Toast);
export default {
  name: "index",
  data() {
    return {
      tianqi: tianqi,
      wenlu: wenlu,
      dadianhua: dadianhua,
      lvyou: lvyou,
      wenhua: wenhua,
      meishi: meishi,
      defaultImg: defaultImg,
      hot_default_img: hot_default_img,
      code: "",
      userInfos: {},
      recList: [],
      popList: [],
      reccoure: []
    };
  },
  beforeCreate() {},
  mounted() {
    this.getUser();
  },
  methods: {
    getUrlParam(name) {
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      var r = window.location.search.substr(1).match(reg);
      if (r != null) return unescape(r[2]);
      return null;
    },
    getUser() {
      var self = this;
      let lag = localStorage.getItem("lag");
      let login_token = localStorage.getItem("login_token");
      if (lag == "" || lag == "undefined" || !lag || lag == "null") {
        lag = this.$route.query["lag"] || "en";
        localStorage.setItem("lag", lag);
      }
      if (
        login_token == "" ||
        login_token == "undefined" ||
        !login_token ||
        login_token == "null"
      ) {
        login_token = this.$route.query["login_token"];
        localStorage.setItem("login_token", login_token);
        if (
          login_token == "" ||
          login_token == "undefined" ||
          !login_token ||
          login_token == "null"
        ) {
          setTimeout(function() {
            window.location.href =
              "http://weikt.apps.skylarkly.com/api/v1/Login/user_login";
            // window.location.href = "http://weikt.webuildus.com/api/v1/Login/user_login";
          }, 300);
        }
      }
      this.http.get("/api/v1/Index/get_home_data?lag=" + lag).then(res => {
        let result = res.data.data;
        if (res.data.code == 200) {
          self.userInfos = result.userinfo;
          self.popList = result.popular_course;
          self.recList = result.recommend_classify;
          self.reccoure = result.recommend_course;
        } else {
          if (res.data.code == -100) {
            localStorage.setItem("login_token", "");
            setTimeout(function() {
              window.location.href =
                "http://weikt.apps.skylarkly.com/api/v1/Login/user_login";
              // window.location.href = "http://weikt.webuildus.com/api/v1/Login/user_login";
            }, 300);
          } else {
            Toast(res.data.msg);
          }
        }
      });

      // var url = window.location.href;
      // if (url.indexOf("login_token") > -1) {
      // } else {
      //   let lag = this.$route.query["lag"] || "cn";
      //   localStorage.setItem("lag", lag);
      //   window.location.href =
      //     "http://weikt.apps.skylarkly.com/api/v1/Login/user_login";
      // }
    },
    more(num) {
      this.$router.push({
        path: "homeList2",
        query: { id: num }
      });
    },
    homeList(num) {
      this.$router.push({
        path: "homeList",
        query: { id: num }
      });
    },
    goDet(id) {
      this.$router.push({
        path: "listdetail",
        query: { id: id }
      });
    }
  }
};
</script>

<style lang="stylus" type="text/stylus">
@import 'index.styl';
</style>

<style lang="less" scoped>
.card {
  padding: 1.5rem 2rem;
  margin-top: 0.5rem;
  position: relative;
  img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  h2 {
    padding: 0;
    margin: 0;
    font-size: 0.9rem;
    font-family: PingFang SC;
    font-weight: Bold;
    color: rgba(255, 255, 255, 1);
    text-align: left;
    position: relative;
    z-index: 1;
  }
  p {
    padding: 0;
    margin: 0;
    font-size: 0.6rem;
    font-family: PingFang SC;
    color: rgba(255, 255, 255, 1);
    margin-top: 0.5rem;
    text-align: left;
    position: relative;
    z-index: 1;
  }
}

.img {
  width: 2.5rem;
  height: 2.5rem;
  background: #eee;
  margin-right: 0.5rem;
  border-radius: 3px;
}

.hot-li-span1 {
  height: 0.9rem;
  text-align: left;
  overflow: hidden;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  text-overflow: ellipsis;
  white-space: normal;
  -webkit-line-clamp: 1;
}

.flex-align-items {
  align-items: center;
}

.padding-lef-right {
  width: 91%;
  padding-left: 3.5%;
}
</style>





