<template>
  <div>
    <div class="content">
      <h2>正文：</h2>
      <div class="white">
        <p> But if you wanna cry <br>Cry on my shoulder<br>If you need someone who cares for you<br>If you"re feeling
          sad your heart gets colder<br>Yes I show you what real love can do<br>
          All my best memories come back clearly to me<br>
          some can even make me cry<br>
          just lick before<br>
          It's yesterday once more</p>
      </div>
    </div>
    <div class="yuyin">
      <img src="@/assets/images/yuyin.png" alt="">
      <p>大声朗读，不断提升</p>
    </div>
    <div class="shiting">
      <van-row>
        <van-col span="8">
          <img src="@/assets/images/icon_shiting.png" alt="">
          <p>试听</p>
        </van-col>
        <van-col span="8">
          <img src="@/assets/images/chonglu.png" alt="">
          <p>重录</p></van-col>
        <van-col span="8">
          <img src="@/assets/images/sahngnchaun.png" alt="">
          <p>上传</p></van-col>
      </van-row>
    </div>
    <van-tabbar v-model="active">
      <van-tabbar-item to="study">
        <span>学习</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active : icon.normal"
        >
      </van-tabbar-item>
      <van-tabbar-item to="practice"><span>练习</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active1 : icon.normal1"
        >
      </van-tabbar-item>
      <van-tabbar-item><span>测验</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active2 : icon.normal2"
        >
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>
<script>
import study from "@/assets/images/xuexi_1.png";
import study_active from "@/assets/images/xuexi.png";
import practice from "@/assets/images/lianxi_1.png";
import practice_active from "@/assets/images/lainxi.png";
import test from "@/assets/images/ceyan_1.png";
import test_active from "@/assets/images/ceyan.png";
import Vue from "vue";
import { Tabbar, TabbarItem, Toast } from "vant";

Vue.use(Tabbar).use(TabbarItem);
Vue.use(Toast);
export default {
  data() {
    return {
      active: 2,
      icon: {
        normal: study,
        active: study_active, //自定义图标1
        normal1: practice,
        active1: practice_active, //自定义图标2
        normal2: test,
        active2: test_active //自定义图标3
      }
    };
  },
  components: {},
  created() {},
  computed: {},
  mounted() {
    this.getConfig();
  },
  filters: {
    formatDate(time) {
      var date = new Date(time);
      return formatDate(date, "yyyy-MM-dd");
    }
  },
  methods: {
    getConfig() {
      let self = this;
      this.http.get("/api/v1/Common/generate_config").then(res => {
        if (res.data.code != 200) {
          Toast(res.data.msg);
        }
      });
    }
  }
};
</script>
<style lang="less" type="text/less" scoped>
.van-tabbar-item--active {
  color: #4fdaa9;
}

* {
  box-sizing: border-box;
}

.shiting {
  width: 50%;
  margin: 0 auto;
  position: fixed;
  bottom: 4.68rem;
  left: 25%;
  img {
    width: 0.98rem;
    height: 0.9rem;
  }
  p {
    font-size: 0.75rem;
    font-weight: normal;
    font-stretch: normal;
    letter-spacing: 0rem;
    color: #4fdaa9;
  }
}

.yuyin {
  img {
    height: 2.15rem;
  }
  position: fixed;
  width: 100%;
  text-align: center;
  bottom: 4.68rem;

  p {
    font-family: PingFang-SC-Medium;
    font-size: 0.7rem;
    font-weight: normal;
    font-stretch: normal;
    letter-spacing: 0rem;
    color: #999999;
  }
}

.content {
  padding: 0.73rem;
  text-align: left;
  h2 {
    font-family: PingFang-SC-Bold;
    font-size: 0.75rem;
    font-weight: normal;
    font-stretch: normal;
    letter-spacing: 0rem;
    color: #000000;
  }
  .white {
    margin-top: 0.88rem;
    padding: 0.9rem 1.68rem 1.43rem 0.68rem;
    width: 100%;
    background-color: #ffffff;
    box-shadow: 0rem 0.25rem 0.5rem 0rem rgba(79, 218, 169, 0.1);
    border-radius: 0.5rem;
    p {
      font-family: PingFang-SC-Medium;
      font-size: 0.7rem;
      font-weight: normal;
      font-stretch: normal;
      color: #333333;
      line-height: 1.1rem;
    }
  }
}
</style>



